import React from 'react'
import Modal from 'react-modal';
import '../css/cast.css'

interface CastBoxProps{
	name: string,
	description: string
}

const CastBox: React.FC<CastBoxProps> = (props) => {

	const [popUpShowed, setPopUpShowed] = React.useState(false);
	const openPopUp = React.useCallback(() => {
		setPopUpShowed(true);
	}, []);
	const closePopUp = React.useCallback(() => {
		setPopUpShowed(false);
	}, [])

	const Ret: React.FC = () => {
		return (
			<button className = "castbox" onClick = {openPopUp}>
				<div className = "castbox-name">{props.name}</div>
				<img className = "castbox-img" src = "/img/dummy.png" />
				<div className = "castbox-description">{props.description}</div>
			</button>
		);
	}
	if(popUpShowed){
		return (
			<>
			<Ret />
				<Modal
					isOpen = {popUpShowed}
					onRequestClose = {closePopUp}
					style = {{
						content: {
							display: "flex",
							justifyContent: "center",
							alignItems: "center",
							height: "40vmin",
							width: "70vmin",
							margin: "auto",
							backgroundColor: "black"
						},
						overlay: {
							background: "rgba(128,128,128,0.75)"
						}
					}}
				>
					<div className = "modal-main">
						<div className = "modal-name">星野 佑希</div>
						<div className = "modal-left">
							<img src = "/img/casts/hoshino.jpg" className = "modal-img"/>
						</div>
						<div className = "modal-right">
						<img src = "/img/dummy.png" className = "modal-combined" />
						<div className = "modal-description">
							<p>
								この物語の主人公であるとともに、筑駒革命軍のリーダー。
								自治会が文化祭を廃止したことに疑問を感じ、筑駒革命軍を結成する。
							</p>
						</div>
						</div>
					</div>
				</Modal>
			</>
		);
	}
	return <Ret />;
}

const Cast: React.FC = (props) => {
	return (
		<div className = "main-container">
			<div className = "main-contents">
				<div className = "castboxes">
					<div className = "castbox-kind">筑駒革命軍</div>
					<CastBox 
						name = "星野 佑希"
						description = "筑駒革命軍のリーダー"
					/>
					<CastBox 
						name = "松下 秀輝"
						description = "筑駒革命軍のブレーン"
					/>
					<CastBox 
						name = "本田 真"
						description = "筑駒革命軍の中心人物"
					/>
					<div className = "castbox-kind">筑駒防衛軍</div>
					<CastBox
						name = "三好 政彦"
						description = "筑駒自治会長"
					/>
					<CastBox
						name = "角田 直大"
						description = "筑駒自治会副会長"
					/>
					<CastBox
						name = "大島 健"
						description = "筑駒自治会会計"
					/>
					<CastBox
						name = "福本 洋"
						description = "筑駒自治会書記"
					/>
					<div className = "castbox-kind">その他</div>
					<CastBox
						name = "鳥井"
						description = "2年2組担任"
					/>
				</div>
			</div>
		</div>
	);
}

export default Cast;